.Legend {
    background-color: $containerBackgroundColor;
    border-radius: $cardBorderRadius;
    border: 1px solid $cardBorderColor;
    padding: $cardPadding;
    margin-bottom: $cardMarginBottom;
}

.Row {
    display: flex;
    flex-direction: row;
    gap: 15px;
}

.Column {
    display: flex;
    flex-direction: column;
}
